import _ from 'lodash';
import React, { Component } from 'react';
import { Button, Icon } from 'antd';

import MainCalendar from './Calendar';
import RightAside from './RightAside';
import Sidebar from './Sidebar';
import CreationModal from './CreationModal';
import moment from 'moment';

const events = [];

class App extends Component {
  state = {
    date: moment().format(),
    view: 'month',
    isModalOpen: false
  };

  handleViewChange = view => this.setState({ view });

  handleDayChange = date => this.setState({ date: moment(date).format() });

  handleEventDrop = (event, delta) => {
    const { id } = event;

    events.forEach(event => {
      if (event.id === id) {
        if (event.start) {
          const prevDateLength = event.start.length;
          event.start = moment(event.start).add(delta).format().substring(0, prevDateLength);
        }
        if (event.end) {
          const prevDateLength = event.end.length;
          event.end = moment(event.end).add(delta).format().substring(0, prevDateLength);
        }
      }
    });
  };

  handleEventResize = (event, delta) => {
    const { id } = event;

    events.forEach(event => {
      if (event.id === id) {
        if (event.end) {
          const prevDateLength = event.end.length;
          event.end = moment(event.end).add(delta).format().substring(0, prevDateLength);
        } else {
          const prevDateLength = event.start.length;
          event.end = moment(event.start).add(delta).add(1, 'd').format().substring(0, prevDateLength);
        }
      }
    });
  };

  handleMonthChange = date => this.setState(prevState => {
    const month = moment(date).month();
    const prevMonth = moment(prevState.date).month();
    if (prevMonth !== month) {
      return { date };
    }

    return { date: moment(prevState.date).month(month).format() };
  });

  handleModalClose = () => this.setState({ isModalOpen: false });

  handleModalOpen = () => this.setState({ isModalOpen: true });

  handleModalSubmit = ({ title, startDate, startTime, endDate, endTime, color }) => {
    const event = {
      id: _.uniqueId('test'),
      title
    };
    if (startTime) {
      event.start = startDate.add(startTime).format();
    } else {
      event.start = startDate.format('YYYY-MM-DD');
    }

    if (endDate) {
      if (endTime) {
        event.end = endDate.add(endTime).format();
      } else {
        event.end = endDate.format('YYYY-MM-DD');
      }
    }

    switch (color) {
      case 'red':
        event.color = '#ffb8a9';
        event.textColor = '#230F0F';
        break;
      case 'yellow':
        event.color = '#fffcb8';
        event.textColor = '#23110F';
        break;
      case 'green':
        event.color = '#bbffbb';
        event.textColor = '#0f230e';
        break;
      case 'purple':
        event.color = '#dfbbe8';
        event.textColor = '#25162d';
        break;
      default:
        event.color = '#b2d8ff';
        event.textColor = '#0B1123';
        break;
    }

    console.log(event);
    events.push(event);
    this.setState({ isModalOpen: false });
  };

  render() {
    const { date, view, isModalOpen } = this.state;

    return (
      <div className="App">
        <Sidebar />
        <MainCalendar
          events={events}
          date={date}
          view={view}
          onViewChange={this.handleViewChange}
          onEventDrop={this.handleEventDrop}
          onEventResize={this.handleEventResize}
        >
          <Button shape="circle" onClick={this.handleModalOpen}>
            <Icon type="plus" />
          </Button>
        </MainCalendar>
        <RightAside
          events={events}
          date={date}
          onDayChange={this.handleDayChange}
          onMonthChange={this.handleMonthChange}
        />
        <CreationModal
          isOpen={isModalOpen}
          onClose={this.handleModalClose}
          onSubmit={this.handleModalSubmit}
        />
      </div>
    );
  }
}

export default App;
