import moment from 'moment';
import React, { Component } from 'react';
import { Button, Col, DatePicker, Form, Input, Modal, Row, Select, TimePicker } from 'antd';
import ColoredDot from './ColoredDot';

const FormItem = Form.Item;

const COLORS = [
  {
    name: 'Blue',
    value: 'blue'
  },
  {
    name: 'Red',
    value: 'red'
  },
  {
    name: 'Yellow',
    value: 'yellow'
  },
  {
    name: 'Green',
    value: 'green'
  },
  {
    name: 'Purple',
    value: 'purple'
  },
];

class CreationModal extends Component {
  componentWillUnmount() {
    this.props.form.resetFields();
  }

  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        this.props.onSubmit(values);
        this.props.form.resetFields();
      }
    });
  };

  render() {
    const { isOpen, onClose, onSubmit } = this.props;
    const { getFieldDecorator } = this.props.form;

    return (
      <Modal
        visible={isOpen}
        title="Add event"
        onOk={onSubmit}
        onCancel={onClose}
        footer={false}
      >
        <Form onSubmit={this.handleSubmit} className="creation-form">
          <FormItem>
            {getFieldDecorator('title', {
              rules: [{ required: true, message: 'Please input title!' }],
            })(
              <Input autoComplete="off" placeholder="Title" />
            )}
          </FormItem>
          <FormItem>
            <Row gutter={8}>
              <Col span={14}>
                {getFieldDecorator('startDate', {
                  initialValue: moment(),
                  rules: [{ required: true, message: 'Please input start date!' }],
                })(
                  <DatePicker style={{ width: '100%' }} placeholder="Start date" />
                )}
              </Col>
              <Col span={10}>
                {getFieldDecorator('startTime', {})(
                  <TimePicker style={{ width: '100%' }} placeholder="Start time" />
                )}
              </Col>
            </Row>
          </FormItem>

          <FormItem>
            <Row gutter={8}>
              <Col span={14}>
                {getFieldDecorator('endDate', {})(
                  <DatePicker style={{ width: '100%' }} placeholder="End date" />
                )}
              </Col>
              <Col span={10}>
                {getFieldDecorator('endTime', {})(
                  <TimePicker style={{ width: '100%' }} placeholder="End time" />
                )}
              </Col>
            </Row>
          </FormItem>

          <FormItem>
            {getFieldDecorator('color', {
              initialValue: 'blue',
            })(
              <Select style={{ width: '100%' }}>
                {COLORS.map(({ name, value }) => (
                  <Select.Option value={value}>
                    <ColoredDot color={value} />
                    {name}
                  </Select.Option>
                ))}
              </Select>
            )}
          </FormItem>

          <FormItem className="creation-form-footer">
            <Button type="primary" htmlType="submit" className="login-form-button">
              Create
            </Button>
          </FormItem>
        </Form>
      </Modal>
    );
  }
}

const WrappedCreationModal = Form.create()(CreationModal);

CreationModal.propTypes = {};

export default WrappedCreationModal;
