import React from 'react';
import FullCalendar from 'fullcalendar-reactwrapper';
import moment from 'moment';
import { Radio } from 'antd';

class ExampleComponent extends React.Component {
  render() {
    const { events, date, view, children, onViewChange, onEventDrop, onEventResize } = this.props;

    return (
      <div className="main-calendar-container">
        <div className="header">
          {children}
          <div className="current-date">{moment(date).format('MMMM D, YYYY')}</div>
          <Radio.Group size="small" value={view} onChange={({ target }) => onViewChange(target.value)}>
            <Radio.Button size="small" value="agendaDay">day</Radio.Button>
            <Radio.Button size="small" value="agendaWeek">week</Radio.Button>
            <Radio.Button size="small" value="month">month</Radio.Button>
          </Radio.Group>
        </div>
        <FullCalendar
          id="calendar-view"
          header={{
            left: '',
            center: 'title',
            right: 'agendaDay,agendaWeek,month'
          }}
          defaultDate={date}
          editable
          eventLimit // allow "more" link when too many events
          events={events}
          defaultView={view}
          eventDrop={onEventDrop}
          eventResize={onEventResize}
        />
      </div>
    );
  }
}

export default ExampleComponent;
