import React from 'react';
import PropTypes from 'prop-types';
import moment from 'moment';
import DayPicker from 'react-day-picker';

import 'react-day-picker/lib/style.css';

import CalendarList from './CalendarList';

const RightAside = ({ events, date, onDayChange, onMonthChange }) => (
  <aside className="right-aside">
    <div className="current-date">{moment(date).format('dddd ● D MMMM')}</div>
    <DayPicker
      selectedDays={new Date(date)}
      todayButton="Go to Today"
      onTodayButtonClick={onDayChange}
      onDayClick={onDayChange}
      onMonthChange={onMonthChange}
    />
    <CalendarList events={events} date={date} />
  </aside>
);

RightAside.propTypes = {
  date: PropTypes.string,
  events: PropTypes.array,
  onDayChange: PropTypes.func,
  onMonthChange: PropTypes.func,
};

export default RightAside;
